<template>
  <ListContainer :title="title" @onClick="handleToMore('/video')">
    <VideoList :dataSource="dataSource" />
  </ListContainer>
</template>
<script>
import ListContainer from "@/components/ListContainer";
import VideoList from "@/components/VideoList";
import { videos } from "../../fakeData";
import mobileMixin from "../../mixin/mobileMixin";

const fakeVideo = videos.slice(0, 20);

export default {
  mixins: [mobileMixin],
  components: {
    ListContainer,
    VideoList,
  },
  data() {
    return {
      fakeVideo,
    };
  },
  computed: {
    dataSource() {
      return this.fakeVideo.map((item) => ({
        ...item,
        path: `/video/${this.category}`,
      }));
    },
    category() {
      return this.$route.fullPath.includes("/jp") ? "jp" : "ch";
    },
    title() {
      const title = this.category === "jp" ? "日韓視頻" : "國產視頻";
      return `${title} ${this.type}`;
    },
    type() {
      return this.$route.params.type || "";
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
  methods: {
    handleAddAds() {
      this.fakeVideo = this.addAds(this.fakeVideo, 4);
    },
    handleRemoveAds() {
      this.fakeVideo = this.removeAds(this.fakeVideo);
    },
  },
};
</script>
